export default "body {\n    padding: 0;\n    margin: 0;\n}\n\nul {\n    padding: 0;\n    margin: 0;\n}\n\n#slider-wrapper {\n    max-width: 540px;\n    width: auto;\n    height: auto;\n    margin: 50px auto;\n    background-color: #10232b;\n    padding: 15px;\n}\n\n#image-slider {\n    width: auto;\n    height: auto;\n    position: relative;\n    overflow: hidden;\n    margin: auto;\n    display: block;\n}\n\n#image-slider ul {\n    width: 100%;\n    height: 359px;\n    position: relative;\n}\n\n#image-slider ul li {\n    display: inline-block;\n    position: absolute;\n    top: 0;\n    left: 540px;\n    width: 100%;\n    height: 100%;\n}\n\n#image-slider ul li img {\n    width: 100%;\n    height: auto;\n}\n\n#image-slider ul li.active-img {\n    left: 0;\n}\n\n.next-img {\n    z-index: 9999;\n}\n\n#thumbnail {\n    cursor: default;\n    margin-top: 3px;\n    height: auto;\n    display: table;\n    width: 100%;\n}\n\n#thumbnail ul {\n    width: 100%;\n    height: 100%;\n}\n\n#thumbnail li {\n    list-style: none;\n    float: left;\n    cursor: pointer;\n    min-width: 1px;\n}\n\n#thumbnail li img {\n    padding: 0;\n    float: left;\n    margin: 0px 1.5px;\n    max-height: 56px;\n    height: 56px;\n}\n\n#thumbnail li:first-child img {\n    margin-right: 0px;\n    margin-left: 0px;\n}\n\ngallery{\n    background: #ECEFF1;\n  }\n\n.carousel {\n    width: 100%;\n    height: 100%;\n    min-height: var(--min-height-carousel);\n    display: flex;\n    flex-direction: column;\n    flex-wrap: nowrap;\n  }\n\n.slides {\n    position: relative;\n    flex-grow: 1;\n    overflow: hidden;\n    list-style: none;\n    margin: var(--unit-1);\n    border: 2px solid var(--color-brand-1);\n  }\n\n.slide {\n    display: block;\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n  }\n\n/* navigation */\n\n.navigations {\n    display: flex;\n    padding: var(--unit-1);\n    border: 2px solid var(--color-brand-1);\n    margin: var(--unit-1);\n  }\n\n.navigations > div {\n    flex: 1;\n  }\n\n.navigations button {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    color: var(--color-brand-1);\n    flex-shrink: 0;\n    padding: 0 calc(var(--unit-1) * 2);\n    background-color: transparent;\n    margin: 0;\n    border-color: transparent;\n    font-weight: bold;\n    outline: none;\n    cursor: pointer;\n    width: calc(var(--unit-1) * 16);\n    text-align: center;\n    border: 2px solid var(--color-brand-1);\n  }\n\n.thumbnails {\n    list-style: none;\n    display: flex;\n    flex-direction: row;\n    flex-grow: 1;\n    flex-wrap: wrap;\n    justify-content: center;\n    margin: 0;\n    padding: 0;\n    overflow: hidden;\n  }\n\n.thumbnails li {\n    cursor: pointer;\n    padding: var(--unit-1);\n    border: 2px solid transparent;\n    width: calc(var(--unit-1) * 12);\n    height: calc(var(--unit-1) * 12);\n    opacity: 0.2;\n    transition: opacity var(--transition-duration) var(--transition-function);\n  }\n\n.thumbnails li.is-active {\n    border-color: var(--color-brand-1);\n    opacity: 1;\n  }\n\n/* .basic-container {\n    padding: 5px;\n  }\n  .container {\n    display: flex;\n    height: 200px !important;\n  }\n  .img {\n    width: 200px !important;\n    object-fit: cover;\n    margin: 5px;\n  }\n  .g-overlay {\n    min-width: 320px !important;\n  }\n  img {\n    width: 150px !important;\n  }\n  \n  .lightbox-example, .gallerize-example {\n    margin: 2em;\n  } */\n\n\n\n/*# sourceMappingURL=data:application/json;base64, */";